import React from "react"
import PropTypes from "prop-types"
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  HatenaShareButton,
  HatenaIcon,
} from "react-share"

const Sns = ({ title, url, via }) => {
  return (
    <div>
      <TwitterShareButton
        className="sharebtn"
        title={title}
        via={via}
        url={url}
      >
        <TwitterIcon size={32} round={false} />
      </TwitterShareButton>

      <FacebookShareButton className="sharebtn" url={url}>
        <FacebookIcon size={32} round={false} />
      </FacebookShareButton>

      <HatenaShareButton className="sharebtn" title={title} url={url}>
        <HatenaIcon size={32} round={false} />
      </HatenaShareButton>
    </div>
  )
}

Sns.propTypes = {
  title: PropTypes.string,
  url: PropTypes.string,
  via: PropTypes.string,
}

export default Sns
