import React from "react"
import { graphql } from "gatsby"
import { css } from "@emotion/react"
import Img from "gatsby-image"
import LayoutPost from "../components/layoutpost"
import Seo from "../components/seo"
import Toc from "../components/toc"
import Sns from "../components/sns"

const BlogPost = ({ data }) => {
  const post = data.markdownRemark
  const share = data.site
  const slug = post.fields.slug
  const siteUrl = share.siteMetadata.siteUrl
  const twitterUsername = share.siteMetadata.twitterUsername
  const noImage = null
  const haveAnImage = post.frontmatter.topImage
    ? post.frontmatter.topImage.childImageSharp.fluid
    : noImage

  return (
    <LayoutPost>
      <Seo title={post.frontmatter.title} description={post.excerpt} />
      <div
        css={css`
          @media (max-width: 930px) {
            width: 100%;
            padding-right: 0rem;
          }
          box-sizing: border-box;
          float: left;
          padding-right: 3rem;
          margin-bottom: 3rem;
          width: 65%;
        `}
      >
        <Img
          fluid={haveAnImage}
          css={css`
            margin-bottom: 2rem;
          `}
        />
        <h1
          css={css`
            font-size: 2.5rem;
            margin-bottom: 0.3rem;
          `}
        >
          {post.frontmatter.title}
        </h1>

        <div
          css={css`
            font-size: 1rem;
            margin-bottom: 1.5rem;
          `}
        >
          <span
            css={css`
              color: hsla(0, 0%, 0%, 0.6);
              font-size: 0.8rem;
            `}
          >
            およそ
          </span>
          <span
            css={css`
              color: hsla(0, 0%, 0%, 0.6);
            `}
          >
            {post.timeToRead}
            {""}
          </span>
          <span
            css={css`
              color: hsla(0, 0%, 0%, 0.6);
              font-size: 0.8rem;
            `}
          >
            分で読める内容
          </span>
        </div>
        <div dangerouslySetInnerHTML={{ __html: post.html }} />
        <div
          css={css`
            font-size: 1rem;
            margin-bottom: 2rem;
            margin-top: 3rem;
          `}
        >
          {post.frontmatter.date}
        </div>
        <Sns
          title={post.frontmatter.title}
          url={`${siteUrl}${slug}`}
          via={twitterUsername}
        />
      </div>

      <Toc toc={post.tableOfContents} />
    </LayoutPost>
  )
}

export const pageQuery = graphql`
  query BlogPostQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents
      timeToRead
      frontmatter {
        title
        date(formatString: "YYYY.MM.DD")
        topImage {
          childImageSharp {
            fluid(maxWidth: 640) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
      excerpt
      fields {
        slug
      }
    }
    site {
      siteMetadata {
        siteUrl
        twitterUsername
      }
    }
  }
`

export default BlogPost
